<template>
  <div class="zoehis_date_editor">
    <zoehis-input
      ref="reference" readonly :clearable="false"
      @focus="focusEvn" :width="200"
      v-model="displayValue">
    </zoehis-input>
    <zoehis-date-editor-box
      ref="popper"
      :initfocus="initfocus"
      :endfocus="endfocus"
      :nowtime="nowtime"
      :value="currentValue"
      :format="format"
      v-if="visible"
      @save="saveEvn"
      @box-blur="boxBlurEvn"
      @tab-end="tabEndEvn"
      @after-prev="afterPrevEvn"
    ></zoehis-date-editor-box>
  </div>
</template>
<script type="text/babel">
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisDateEditorBox from "./date-editor-box.vue";
  import { isDate } from '../../../src/utils/util';
  import dateUtil from '../../../src/utils/date';
  export default {
    name: 'ZoehisDateEditor',
    componentName: 'ZoehisDateEditor',
    components:{ZoehisDateEditorBox},
    mixins: [Emitter],
    data(){
      return {
        displayVal:"",
        visible:false,
        currentValue:null
      }
    },
    props:{
      value: {
        required: true
      },
      //
      format: {
        type:String,default:"yyyy-MM-dd HH:mm:ss"
      },
      valueFormat:{//v-model  1.""   2.timestamp:  3.yyyy-MM-dd HH:mm:ss / yyyy-MM-dd 
        type:String,default:""
      },
      //input  format
      inputformat: {
        type:String,default:""
      },
      //  year/month/date/hour/minute/seconds
      initfocus:{
        type:String,default:"year"
      },
      endfocus:{type:String,default:"seconds"},// entertab 
      //v-model  true
      nowtime:{
        type:Boolean,default:true
      }

    },
    watch:{
      //v-model
      value:{
        immediate:true,//value
        handler(val){
          this.currentValue = isDate(val) ? new Date(val) : val; //
        }
      },
      visible(val){
        if(!val){

        }else {
          this.$nextTick(()=>{
            this.broadcast('ZoehisDateEditorBox', 'updatePopper'); //()
          });
        }
      }
    },
    methods:{
      // ctrl+tab  /  ctrl+enter
      afterPrevEvn(){
        this.$emit("after-prev");
      },
      focusEvn(){
        this.visible = true;
        this.$nextTick(()=>{
          this.$refs.popper.selectInput(this.initfocus);
        });
        this.$emit("focus")
      },
      //
      boxBlurEvn(){
        this.visible = false;
      },
      //tab 
      tabEndEvn(){
        this.$emit("tab-end");
        this.visible = false;
      },
      //
      //date 
      saveEvn(date,type){
        //
        if(type=="clear"){
          //
          if(this.currentValue){
            this.$emit("input","",type);
            this.$emit("change","",type);//
          }
          this.$emit("clear");
          this.visible = false;
          return;
        }

        //
        // ==
        if(!date && !this.currentValue){
          this.$emit("save","",type);
          this.visible = false;
          return;
        }

        //  this.currentValue
        if(!date){
          this.$emit("input","",type);
          this.$emit("change","",type);
          this.$emit("save","",type);
          this.visible = false;
          return;
        }

        // date
        // date
        let val = date;
        if(!this.valueFormat){
          val=date;
        }else if(this.valueFormat=="timestamp"){
          val = date.getTime();
        }else {//
          val = dateUtil.format(date,this.valueFormat||"yyyy-MM-dd");
        }

        // 
        if(this.currentValue && dateUtil.format(this.currentValue,"yyyy-MM-dd HH:mm:ss")==dateUtil.format(date,"yyyy-MM-dd HH:mm:ss")){
          this.$emit("save",val,type);
          this.visible = false;
          return;
        }

        this.$emit("input",val,type);
        this.$emit("change",val,type);
        this.$emit("save",val,type);
        this.visible = false;
      },

      //focus
      focus(){
        this.$refs.reference.focus();
      },
      //hide 
      hide(){
        this.visible = false;
      }
    },
    computed:{
      displayValue:{
        //
        get(){
          const value = this.currentValue;
          if(!value) return "";
          return dateUtil.format(value,this.inputformat||this.format||"yyyy-MM-dd");
        },
        set(value){
          //
//          if(value){
//            const type = this.type;
//            const parser = (
//              TYPE_VALUE_RESOLVER_MAP[type] ||
//              TYPE_VALUE_RESOLVER_MAP['default']
//            ).parser;
//            //
//            const parserValue = parser(value,this.format || DEFAULT_FORMATS[type]);
//            //picker
//            if(parserValue){
//              //
//              parserValue.setFullYear(this.currentValue.getFullYear());
//              parserValue.setMonth(this.currentValue.getMonth(),this.currentValue.getDate());
//              if(this.picker){//picker 
//                let t1 = isDate(parserValue) ? new Date(parserValue).getTime() : parserValue; //
//                let t2 = isDate(this.picker.value) ? new Date(this.picker.value).getTime() : this.picker.value; //
//                if(t1==t2){
//                  this.$refs.reference.setValue(this.displayValue);
//                }else {
//                  this.picker.value = parserValue;
//                }
//              }else {//pickerinput
//                this.$emit('input', this.standardtype?parserValue:parserValue.getTime());
//              }
//            }
//
//          }else {//
//            this.$emit('input', value);
//            this.picker.value = value;
//          }
        }
      }
    },
    created(){

    }
  };
</script>



