<template>
  <div class="component_example">
    <h1>Button </h1>
    <h2></h2>
    <h4>30px</h4>
    <h4>26px</h4>
    <div class="item">
      <h4></h4>
      <div class="item-content" id="testtest">
        <zoehis-button icon="z_delete_normal"></zoehis-button>

        <zoehis-button type="primary" icon="z_delete_normal"></zoehis-button>
        <zoehis-button icon="z_delete_normal"></zoehis-button><zoehis-button icon="z_delete_normal" type="primary"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;

            &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
            &lt;zoehis-button&gt;&lt;/zoehis-button&gt;&lt;zoehis-button type=&quot;primary&quot;&gt;&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          type2default/primary/<br>
          icon<br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" :disabled="false" icon="z_delete_normal">()</zoehis-button>
        <zoehis-button type="primary" :disabled="true" icon="z_delete_normal">()</zoehis-button>
        <zoehis-button :disabled="false" icon="z_delete_normal"></zoehis-button>
        <zoehis-button  icon="z_delete_normal"type="primary" :disabled="true"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type="primary" :disabled="true" &gt;&lt;/zoehis-button&gt;<br/>
            &lt;zoehis-button :disabled="true" &gt;&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          disabled2true/false/ <br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal"></zoehis-button><zoehis-button type="primary" size="small"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type="primary"  &gt;&lt;/zoehis-button&gt;<br/>
            &lt;zoehis-button type="primary" size="small" &gt;&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          size2normal/small/ <br>
           normal
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal" keyboard="0">()</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot; keyboard=&quot;0&quot;&gt;()&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          keyboardString
        </div>
      </div>
    </div>
    <div class="item">
      <h4>/enter</h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal" @clickenter="myClickEnterEve"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button @clickenter="myClickEnterEve"&gt;&lt;/zoehis-button&gt;<br/>
            &lt;zoehis-button type="primary" @clickenter="myClickEnterEve"&gt;&lt;/zoehis-button&gt;<br/>
          </code></pre>
          <pre><code>
            //
            methods:{
              myClickEnterEve:function(evt){
                alert("");
              }
            }
          </code></pre>
        </div>
        <div class="item-desc">
          @clickenter="myClickEnterEve",/enterenter <br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button-group>
          <zoehis-button type="primary" icon="z_delete_normal"></zoehis-button><zoehis-button icon="z_delete_normal" type="default"></zoehis-button>
        </zoehis-button-group>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button-group&gt;
              &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
              &lt;zoehis-button type=&quot;default&quot;&gt;&lt;/zoehis-button&gt;
            &lt;/zoehis-button-group&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          zoehis-button-group
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary" :keyboard="keyboard" icon="z_delete_normal">{{featuresName}}</zoehis-button>
        <zoehis-button type="default" icon="z_delete_normal">{{defaultName}}</zoehis-button>
        <zoehis-button icon="z_delete_normal" @clickenter="changeBtnCt" type="primary"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type=&quot;primary&quot; :keyboard=&quot;keyboard&quot; icon=&quot;z_delete_normal&quot;&gt;{{featuresName}&lt;/zoehis-button&gt;
            &lt;zoehis-button type=&quot;default&quot;&gt;{{defaultName}&lt;/zoehis-button&gt;
          </code></pre>
          <pre><code>
            data() {
              return {
                featuresName:"",
                defaultName:"",
                keyboard:"2"
              }
            },
            methods:{
              changeBtnCt:function(evt){
                this.defaultName="";
                this.featuresName="";
                ++this.keyboard;
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          keyboard defaultNamefeaturesName
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr><td>type</td><td></td><td>string</td><td>default/primary/</td><td>default</td></tr>
        <tr><td>icon</td><td></td><td>string</td><td></td><td>-</td></tr>
        <tr><td>disabled</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>size</td><td></td><td>string</td><td>'normal'/'small'</td><td>'normal'</td></tr>
        <tr><td>keyboard</td><td></td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>@clickenter</td><td>/enter</td><td>1.e</td><td>-</td><td>-</td></tr>
        <tr><td>focus()</td><td></td><td></td><td>-</td><td>-</td></tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'button',
    data() {
      return {
        featuresName:"",
        defaultName:"",
        keyboard:"2"
      }
    },
    methods:{
      changeBtnCt:function(evt){
        this.defaultName="";
        this.featuresName="";
        ++this.keyboard;
      },
      myClickEnterEve:function(evt){
        alert("/enter");
      },
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



